dd {
    margin-inline-start: 0;
}

.app-greetings {
    background-image: url(../img/Main.svg);
    max-width: 1440px;
    height: 640px;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: cover;
    color: #fff;
    display: flex;
    flex-direction: column;
}

.text-greetings {
    margin: 110px auto 20px auto;
    font-weight: normal;
    font-size: 40px;
}

.white-beans {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 25px;
}

.white-line {
    width: 60px;
    height: 0px;
    border: 0.5px solid;
    color: #fff;
}

.text {
    margin: 35px auto 20px auto;
    font-weight: normal;
    font-size: 24px;
}

.text-question {
    margin: 0px auto 200px auto;
    font-weight: normal;
    font-size: 24px;
}
