.app-products {
    background-image: url(../img/Main-products.svg);
    max-width: 1440px;
    height: 100%;
    margin: 0 auto;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
}

.text-products {
    margin: 80px auto 40px auto;
    color: #000000;
    font-weight: normal;
    font-size: 24px;
}

.coffee-assortment {
    margin: 0 auto 110px auto;
    gap: 70px;
    display: flex;
}

.coffee-product {
    display: flex;
    flex-direction: column;
    background-color: rgba(253, 253, 253, 0.603);
    width: 220px;
    height: 240px;
    border-radius: 8px;
    font-size: 14px;
    align-items: center;
    cursor: pointer;
}

.product-photo-first {
    padding: 20px 45px 14px 45px;
    text-align: center;
}

.product-photo-second {
    padding: 20px 45px 10px 45px;
    text-align: center;
}

.product-photo-third {
    padding: 37px 45px 14px 45px;
    text-align: center;
}

.text-describe {
    margin: 0 auto 10px auto;
    font-weight: 14px;
}

.product-price {
    text-align: end;
    margin: 0px 20px 20px 150px;
    font-weight: bold;
    font-weight: 14px;
}