.search-menu {
    max-width: 1440px;
    margin: 0 auto 60px auto;
    display: flex;
    flex-direction: column;
}

.filter-menu {
    display: flex;
    justify-content: center;
    margin: 0 auto 30px auto;
}

.input-area {
    display: flex; 
    align-items: baseline;
}

.input-prop {
    box-shadow: 13px -5px 57px 0px rgb(192, 191, 191);
    border-radius: 3px;
    border: none;
    text-align: center;
}

.filter-area {
    padding-left: 80px;
    display: flex;
    align-items: baseline;
}

.btn-search {
    border: none;
    background-color: #fff;
    box-shadow: 13px -5px 57px 0px rgb(192, 191, 191);
    margin-right: 5px;
    padding: 5px 20px 5px 20px;
    font-family: inherit;
}

.countries {
    display: inline-block;
    background: #fff;
    box-shadow: 13px -5px 57px 0px rgb(192, 191, 191);
    border-radius: 5px;
    padding: 5px 20px 5px 20px;
    margin-right: 10px;
    font-family: inherit;
    font-size: 12px;
    cursor: pointer;
    border: 1px solid transparent;
    transition: all 500ms ease;
}

.countries:hover {
    transform: scale(1.1);
    border-color: silver;
}



.choose-product {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.product-column {
    display: flex;
    gap: 70px;
    margin: 30px 320px 30px 320px;
    font-size: 14px;
    text-align: end;
    flex-wrap: wrap;
}

.coffee-item {
    width: 220px;
    height: 100%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 14px 18px 23px 1px rgb(197, 197, 197);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.coffee-photo {
    padding: 37px 26px 14px 26px;
}

.country {
    margin: 5px 24px 5px auto;
}

.price {
    margin: 7px 25px 20px 157px;
}

