.app-info {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    height: 100%;
    max-width: 1440px;
    margin: 0 auto;
}

.about-us {
    margin: 80px auto 40px auto;
    font-weight: normal;
    font-size: 24px;
    color: #000000;
}

.black-beans {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 25px;
}

.black-line {
    width: 60px;
    height: 0px;
    border: 0.5px solid;
    color: #000000;
}

.text-description {
    margin: 0 auto 100px auto;
    width: 590px;
    height: 223px;
    text-align: center;
    font-size: 14px;
}