.beans-decsription {
    max-width: 1440px;
    height: 100%;
    margin: 0 auto;
    display: flex;
    align-items: stretch;
    flex-direction: column;
}

.content-area {
    display: flex;
    margin: 0 auto;
    max-width: 1440px;
}

.coffee-drink {
    padding: 70px 80px 60px 280px;
}

.text-about {
    display: flex;
    flex-direction: column;
    padding: 80px 350px 70px 0;
    justify-content: center;
    align-items: center;
}

.about-beans {
    font-size: 24px;
}

.text-item {
    width: 350px;
    height: 230px;
    text-align: center;
    font-size: 14px;
}

.end-line {
    width: 240px;
    color:#000000;
    height: 0;
    border: 0.5px solid;
    margin: 0 auto 60px auto;
}